<template>
  <div class="cate">
    <div class="cate-left">
      <ul>
        <li v-for="(item,index) in leftItem" :key="index" @click="liClick(index)">{{item}}</li>
      </ul>
    </div>
    <scroll class="content" @scroll="contentScroll" :probe-type="3" ref="scroll">
      <goods-list :goods="shownew" class="cate-right" @itemImageLoad="itemImageLoad" />
    </scroll>
    <back-top @click.native="backClick" v-show="isShowBackTop" />
  </div>
</template>

<script>
import { getHomeData } from "network/home";
import GoodsList from "components/content/goods/GoodsList";
import Scroll from "components/common/scroll/Scroll";

import { itemListenerMixin, backTopMixin } from "common/mixin";

export default {
  name: "CateLeft",
  components: {
    getHomeData,
    GoodsList,
    Scroll
  },
  data() {
    return {
      leftItem: [
        "流行",
        "新款",
        "潮流",
        "上衣",
        "裤子",
        "流行",
        "新款",
        "潮流",
        "上衣",
        "裤子",
        "流行",
        "新款",
        "潮流",
        "上衣"
      ],
      new: { page: 0, list: [] },
      index: 1
    };
  },
  mixins: [itemListenerMixin, backTopMixin],
  computed: {
    shownew() {
      //展示的数据的改变
      return this.new.list;
    }
  },

  // activated() {
  //   this.refresh();
  // },
  created() {
    this.getHomeData("new", this.index);
  },
  mounted() {},
  methods: {
    liClick(index) {
      this.index = index + 1;
      this.getHomeData("new", this.index);
      this.$refs.scroll.scrollTo(0, 0, 0);
    },
    //商品展示数据保存
    getHomeData(type, page) {
      getHomeData("pop", page).then(res => {
        //将新加载的数据加到数组内
        // console.log(res);
        this.new.list = [];
        this.new.list = res.data.list;
        // console.log(this.new.list);
      });
    },
    contentScroll(position) {
      //显示回到顶部按钮
      this.isShowBackTop = -position.y >= 200;
    },
    itemImageLoad() {
      this.refresh();
    }
  }
};
</script>

<style scoped>
.cate {
  display: flex;
  /* z-index: 9; */
  width: 100%;
}
.cate-left {
  width: 100px;
  justify-content: flex-start;
}
.cate-right {
  /* padding-bottom: 44px; */
  flex: 1;
}

ul > li {
  font-size: 16px;
  padding: 10px;
  width: 100px;

  border-bottom: 1px solid #eee;
  border-right: 1px solid #eee;
  text-align: center;
  list-style: none;
}
.content {
  height: calc(100% - 91px);
  overflow: hidden;
}
</style>